body {
  background: #e74c3c;
}

.body-game {
  margin: 0px;
  padding: 0px;
  background: #e74c3c;
  font-family: 'Lato', sans-serif;
}

.h1-game{
    color: #fff;
    text-align: center;
    font-weight: 200;
    font-size: 80px;
}

.h2-game{
    color: #fff;
    text-align: center;
    font-weight: 200;
    font-size: 40px;
}



.h1-game .span-game{
  display: block;
  font-size: 40px;

}

.game-div {
  position: absolute;
  align-items: center;
  display: flex;
  justify-content: space-between;
  width: 75%;
}

li {
    list-style: none;
}

li a {
    text-decoration: none;
    color: #fff;
    margin: 10px;
    width: 150px;
    background: #eee;
    padding: 10px;
    display: block;
    text-align: center;
    font-size: 18px;
    cursor: move;
}

ul li:first-child a{ background: #34495e }
ul li:nth-child(2) a{ background: #3498db }
ul li:nth-child(3) a{ background: #f39c12 }
ul li:nth-child(4) a{ background: #27ae60 }
ul li:nth-child(5) a{ background: #34495e }
ul li:nth-child(6) a{ background: #3498db }
ul li:nth-child(7) a{ background: #f39c12 }
ul li:nth-child(8) a{ background: #27ae60 }
ul li:nth-child(9) a{ background: #34495e }
ul li:nth-child(10) a{ background: #9b59b6 }
ul li:nth-child(11) a{ background: #f39c12 }
ul li:nth-child(12) a{ background: #27ae60 }
ul li:nth-child(13) a{ background: #34495e }
ul li:nth-child(14) a{ background: #3498db }
ul li:last-child a{ background: #9b59b6 }


*[draggable=true] {
    -moz-user-select:none;
    -khtml-user-drag: element;
    cursor: move;
}

*:-khtml-drag {
    background-color: rgba(238,238,238, 0.5);
}

li a:hover:after {
    content: '';
}

ul {
    width: 0%;
    margin-left: 0%;
    margin-right: 0%;
    min-height: 300px;
}

li.over {
    border-color: #333;
    background: #ccc;
}

#bin {
    height: 900px;
    width: 0%;
    position: relative;
    margin-top: 0;
    margin-left: 0;
}

.recycle-img {
  width: 300px;
}

#bin2 {
  height: 900px;
  width: 0%;
  position: relative;
}

#bin p {
    font-weight: bold;
    text-align: center;
    position: relative;
    bottom: 20px;
    font-size: 32px;
    color: #fff;
    top: -80px;
}

/*font-face*/
@font-face {
    font-family: 'Lato';
    font-style: normal;
    font-weight: 100;
    src: local('Lato Hairline'), local('Lato-Hairline'), url(http://themes.googleusercontent.com/static/fonts/lato/v6/boeCNmOCCh-EWFLSfVffDg.woff) format('woff');
}
@font-face {
    font-family: 'Lato';
    font-style: normal;
    font-weight: 300;
    src: local('Lato Light'), local('Lato-Light'), url(http://themes.googleusercontent.com/static/fonts/lato/v6/KT3KS9Aol4WfR6Vas8kNcg.woff) format('woff');
}
@font-face {
    font-family: 'Lato';
    font-style: normal;
    font-weight: 400;
    src: local('Lato Regular'), local('Lato-Regular'), url(http://themes.googleusercontent.com/static/fonts/lato/v6/9k-RPmcnxYEPm8CNFsH2gg.woff) format('woff');
}
@font-face {
    font-family: 'Lato';
    font-style: normal;
    font-weight: 700;
    src: local('Lato Bold'), local('Lato-Bold'), url(http://themes.googleusercontent.com/static/fonts/lato/v6/wkfQbvfT_02e2IWO3yYueQ.woff) format('woff');
}
