html, body {
  font-family: Helvetica;
  margin: 0; 
  height: 100%; 
  overflow: hidden;
}
.header{
  background-color: gray;
  height: 60px;
  display: flex; 
  justify-content: space-between;
}

.header-button{
  background-color: #4CAF50;
  color: white;
  padding: 14px 20px;
  margin: 8px 0;
  border: none;
  cursor: pointer;
  width: 15%;
}

.home-container{
  background-color: lightgray;
  min-height: 100vh; 
  min-width: 100vw; 
}

.h1 {
  text-align: center;
  display: flex;
  justify-content: center;
  padding: 80px 12px 12px 12px;
  width: 100%;
  font-weight: bold;
  font-size: 62px;
}

.h1-window {
  text-align: center;
  justify-content: center;
  display: flex;
  font-weight: bold;
}

h2 {
  text-align: center;
  display: flex;
  justify-content: center;
  width: 100%;
  font-weight: bold;
  font-size: 32px;
}
h3 {
  text-align: center;
  display: flex;
  justify-content: center;
  width: 100%;
  font-weight: bold;
  font-size: 22px;
}

input[type=text], input[type=password] {
  width: 100%;
  padding: 12px 20px;
  margin: 8px 0;
  display: inline-block;
  border: 1px solid #ccc;
  box-sizing: border-box;
}

.question {
  display: block;
  text-align: center;
  padding: 8px;
}

select {
  width: 50%;
  padding: 12px 20px;
  margin: 0 auto;
  display: block;
  text-align: center;
  border: 1px solid #ccc;
  box-sizing: border-box;
}

.styled-select {
  height: 29px;
  overflow: hidden;
  width: 240px;
}

.styled-select select {
  background: transparent;
  border: none;
  font-size: 14px;
  height: 29px;
  padding: 5px; /* If you add too much padding here, the options won't show in IE */
  width: 268px;
}

img {
  padding: 10px 0px;
  margin: 0px 0px;
  cursor: pointer;
}

/* Set a style for all buttons */
.menu-button {
  background-color: #4CAF50;
  color: white;
  padding: 14px 20px;
  margin: 8px 0;
  border: none;
  cursor: pointer;
  width: 100%;
}

.screen-div {
  align-items: center;
  align-content: center;
  align-self: auto;
}

.screen-button {
  float: center;
  background-color: #4CAF50;
  color: white;
  padding: 14px 20px;
  margin: 8px 0;
  border: none;
  cursor: pointer;
  width: 30%;
  font-size: 30px;
  size: 30px;
}

button:hover {
  opacity: 0.8;
}

.cancelbtn {
  background-color: #f44336;
}

.container {
  padding: 16px;
}

.table {
  margin-left:auto; 
  margin-right:auto;
}

span.psw {
  float: right;
  padding-top: 16px;
}

/* The Modal (background) */
.modal {
  position: fixed; /* Stay in place */
  z-index: 1; /* Sit on top */
  left: 0;
  top: 0;
  width: 100%; /* Full width */
  height: 100%; /* Full height */
  overflow: auto; /* Enable scroll if needed */
  background-color: rgb(0,0,0); /* Fallback color */
  background-color: rgba(0,0,0,0.4); /* Black w/ opacity */
  padding-top: 60px;
}

/* Modal Content/Box */
.modal-content {
  background-color: #fefefe;
  margin: 5% auto 15% auto; /* 5% from the top, 15% from the bottom and centered */
  border: 1px solid #888;
  width: 60%; /* Could be more or less, depending on screen size */
}



/* Add Zoom Animation */
.animate {
  -webkit-animation: animatezoom 0.6s;
  animation: animatezoom 0.6s
}

@-webkit-keyframes animatezoom {
  from {-webkit-transform: scale(0)} 
  to {-webkit-transform: scale(1)}
}
  
@keyframes animatezoom {
  from {-webkit-transform: scale(0);transform: scale(0)} 
  to {-webkit-transform: scale(1);transform: scale(1)}
}

.leaderboard-modal-content {
  background-color: #fefefe;
  margin: 5% auto 15% auto; /* 5% from the top, 15% from the bottom and centered */
  border: 1px solid #888;
  width: 40%; /* Could be more or less, depending on screen size */
}

.table {
  border-collapse: collapse;
  width: 90%;
}

th, td {
  padding: 14px;
  white-space: nowrap;
}
  
thead th {
  border-bottom: 2px solid indianred;
  font-weight: normal;
  padding-right: 0;
  color: indianred;
  text-align: left;
}
  
tbody td {
  border-bottom: 1px solid 14px;
}

.reactable-pagination {
  position: absolute;
  display: none;
}
body {
  background: #e74c3c;
}

.body-game {
  margin: 0px;
  padding: 0px;
  background: #e74c3c;
  font-family: 'Lato', sans-serif;
}

.h1-game{
    color: #fff;
    text-align: center;
    font-weight: 200;
    font-size: 80px;
}

.h2-game{
    color: #fff;
    text-align: center;
    font-weight: 200;
    font-size: 40px;
}



.h1-game .span-game{
  display: block;
  font-size: 40px;

}

.game-div {
  position: absolute;
  align-items: center;
  display: flex;
  justify-content: space-between;
  width: 75%;
}

li {
    list-style: none;
}

li a {
    text-decoration: none;
    color: #fff;
    margin: 10px;
    width: 150px;
    background: #eee;
    padding: 10px;
    display: block;
    text-align: center;
    font-size: 18px;
    cursor: move;
}

ul li:first-child a{ background: #34495e }
ul li:nth-child(2) a{ background: #3498db }
ul li:nth-child(3) a{ background: #f39c12 }
ul li:nth-child(4) a{ background: #27ae60 }
ul li:nth-child(5) a{ background: #34495e }
ul li:nth-child(6) a{ background: #3498db }
ul li:nth-child(7) a{ background: #f39c12 }
ul li:nth-child(8) a{ background: #27ae60 }
ul li:nth-child(9) a{ background: #34495e }
ul li:nth-child(10) a{ background: #9b59b6 }
ul li:nth-child(11) a{ background: #f39c12 }
ul li:nth-child(12) a{ background: #27ae60 }
ul li:nth-child(13) a{ background: #34495e }
ul li:nth-child(14) a{ background: #3498db }
ul li:last-child a{ background: #9b59b6 }


*[draggable=true] {
    -moz-user-select:none;
    -khtml-user-drag: element;
    cursor: move;
}

*:-khtml-drag {
    background-color: rgba(238,238,238, 0.5);
}

li a:hover:after {
    content: '';
}

ul {
    width: 0%;
    margin-left: 0%;
    margin-right: 0%;
    min-height: 300px;
}

li.over {
    border-color: #333;
    background: #ccc;
}

